import Navigation from '../components/navigation'

const Events = () => {
  return (
    <div>
      <Navigation pageName={'Events'} contactMenuOpenOnLoad={false} />
      <div style={{marginTop: 80, height: 0}} >
        Events
      </div>
    </div>
  )
}

export default Events



